'use client';
import React, { useState } from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';
import VentaDetalleModal from './VentaDetalleModal';
import VentaResumen from './VentaResumen';
import { VentaServicio } from '../../../entities/venta';
import VentaRegistroModal from './VentaRegistroModal';

const VentasTable = () => {
  const { ventas, eliminarVenta } = useVentas();
  const [ventaSeleccionada, setVentaSeleccionada] = useState<VentaServicio | null>(null);
  const [filtroCliente, setFiltroCliente] = useState('');
  const [filtroFecha, setFiltroFecha] = useState('');

  const ventasFiltradas = ventas.filter(v =>
    v.cliente.nombre.toLowerCase().includes(filtroCliente.toLowerCase()) &&
    v.fechaRegistro.includes(filtroFecha)
  );

  return (
    <div className="space-y-6">
      <VentaResumen />

      {/* Filtros */}
      <div className="flex flex-wrap justify-between items-center gap-4">
        <div className="flex gap-3 flex-wrap">
          <input
            type="text"
            placeholder="üîç Buscar cliente"
            className="border p-2 rounded-lg shadow-sm focus:ring focus:border-indigo-500"
            value={filtroCliente}
            onChange={(e) => setFiltroCliente(e.target.value)}
          />
          <input
            type="date"
            className="border p-2 rounded-lg shadow-sm focus:ring focus:border-indigo-500"
            value={filtroFecha}
            onChange={(e) => setFiltroFecha(e.target.value)}
          />
        </div>
        <VentaRegistroModal />
      </div>

      {/* Tabla */}
      <div className="overflow-x-auto rounded-lg shadow border border-gray-200">
        <table className="min-w-full bg-white text-sm">
          <thead className="bg-gray-50 text-gray-600 uppercase text-xs tracking-wider">
            <tr>
              <th className="px-4 py-3 text-left">ID</th>
              <th className="px-4 py-3 text-left">Cliente</th>
              <th className="px-4 py-3 text-left">Fecha</th>
              <th className="px-4 py-3 text-left">Total</th>
              <th className="px-4 py-3 text-left">Acciones</th>
            </tr>
          </thead>
          <tbody>
            {ventasFiltradas.length > 0 ? (
              ventasFiltradas.map((venta) => (
                <tr key={venta.idVentaServicio} className="border-b hover:bg-gray-50 transition">
                  <td className="px-4 py-3">{venta.idVentaServicio}</td>
                  <td className="px-4 py-3">{venta.cliente.nombre}</td>
                  <td className="px-4 py-3">{formatDate(venta.fechaRegistro)}</td>
                  <td className="px-4 py-3 font-bold text-green-700">{formatCurrency(venta.costoTotal)}</td>
                  <td className="px-4 py-3 space-x-2">
                    <button
                      onClick={() => setVentaSeleccionada(venta)}
                      className="inline-flex items-center gap-1 px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white text-sm rounded-md shadow-sm transition"
                    >
                      üëÅ Ver
                    </button>
                    <button
                      onClick={() => eliminarVenta?.(venta.idVentaServicio)}
                      className="inline-flex items-center gap-1 px-3 py-1 bg-red-600 hover:bg-red-700 text-white text-sm rounded-md shadow-sm transition"
                    >
                      üóë Eliminar
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={5} className="text-center text-gray-500 py-4">
                  No hay ventas registradas.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* Modal de detalle */}
      {ventaSeleccionada && (
        <VentaDetalleModal
          venta={ventaSeleccionada}
          onClose={() => setVentaSeleccionada(null)}
        />
      )}
    </div>
  );
};

export default VentasTable;
