'use client';
import React from 'react';
import { VentaServicio } from '../../../entities/venta';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';

type Props = {
  venta: VentaServicio;
  onClose: () => void;
};

const VentaDetalleModal = ({ venta, onClose }: Props) => {
  return (
    <div className="modal-overlay">
      <div className="modal-content max-w-xl">
        <button className="modal-close-btn" onClick={onClose}>
          ‚úñ
        </button>
        <h2 className="text-xl font-bold text-indigo-700 mb-4">üìã Detalle de Venta</h2>

        {/* Info general */}
        <div className="space-y-2 mb-5">
          <p className="text-sm text-gray-600">
            <span className="font-semibold text-gray-800">ID Venta:</span> {venta.idVentaServicio}
          </p>
          <p className="text-sm text-gray-600">
            <span className="font-semibold text-gray-800">Cliente:</span> {venta.cliente.nombre}
          </p>
          <p className="text-sm text-gray-600">
            <span className="font-semibold text-gray-800">Tel√©fono:</span> {venta.cliente.telefono}
          </p>
          <p className="text-sm text-gray-600">
            <span className="font-semibold text-gray-800">Correo:</span> {venta.cliente.correo}
          </p>
          <p className="text-sm text-gray-600">
            <span className="font-semibold text-gray-800">Fecha:</span> {formatDate(venta.fechaRegistro)}
          </p>
        </div>

        {/* Servicios */}
        <div className="mb-6">
          <h3 className="text-md font-semibold text-gray-700 mb-2">üõ† Servicios Incluidos:</h3>
          <ul className="space-y-2">
            {venta.detalles.map((detalle) => (
              <li key={detalle.idDetalleServicio} className="p-3 border rounded-lg bg-gray-50">
                <p className="text-gray-800 font-medium">{detalle.nombreServicio}</p>
                <p className="text-sm text-gray-500">Costo: {formatCurrency(detalle.costoTotal)}</p>
              </li>
            ))}
          </ul>
        </div>

        {/* Totales */}
        <div className="flex justify-end flex-col sm:flex-row sm:items-center sm:justify-between border-t pt-4 gap-2">
          <div className="text-sm text-gray-700">
            Descuento aplicado:{' '}
            <span className="text-red-500 font-medium">{formatCurrency(venta.descuento)}</span>
          </div>
          <div className="text-lg font-bold text-green-700">
            Total: {formatCurrency(venta.costoTotal)}
          </div>
        </div>

        {/* Cerrar */}
        <div className="flex justify-end mt-6">
          <button
            onClick={onClose}
            className="bg-indigo-600 hover:bg-indigo-700 text-white px-5 py-2 rounded-md transition"
          >
            Cerrar
          </button>
        </div>
      </div>
    </div>
  );
};

export default VentaDetalleModal;
