'use client';
import React, { useState } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientes: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'ABC Soluciones', telefono: '987654321', correo: 'abc@empresa.com' },
  { idCliente: 'CL-002', nombre: 'XYZ Market', telefono: '999888777', correo: 'xyz@empresa.com' },
];

const servicios = [
  { idServicio: 'SVC-001', nombre: 'Instalaci√≥n de red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte t√©cnico', costo: 120 },
  { idServicio: 'SVC-003', nombre: 'Cableado estructurado', costo: 300 },
];

const VentaRegistroModal = () => {
  const { registrarVenta } = useVentas();
  const [open, setOpen] = useState(false);
  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);
  const [mensaje, setMensaje] = useState('');
  const [error, setError] = useState('');

  const agregarServicio = (id: string) => {
    const servicio = servicios.find(s => s.idServicio === id);
    if (!servicio || detalles.some(d => d.idServicio === id)) return;
    const nuevoDetalle: DetalleServicio = {
      idDetalleServicio: `DET-${Date.now()}`,
      idServicio: servicio.idServicio,
      nombreServicio: servicio.nombre,
      costoTotal: servicio.costo,
    };
    setDetalles(prev => [...prev, nuevoDetalle]);
  };

  const eliminarDetalle = (id: string) => {
    setDetalles(prev => prev.filter(d => d.idServicio !== id));
  };

  const limpiar = () => {
    setCliente(null);
    setDetalles([]);
    setDescuento(0);
    setMensaje('');
    setError('');
    setOpen(false);
  };

  const total = detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento;

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) {
      setError('‚ö†Ô∏è Selecciona un cliente y al menos un servicio.');
      return;
    }

    const nuevaVenta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: new Date().toISOString(),
    };

    registrarVenta(nuevaVenta);
    setMensaje('‚úÖ Venta registrada con √©xito.');
    limpiar();
  };

  return (
    <>
      <button
        onClick={() => setOpen(true)}
        className="bg-gradient-to-r from-purple-600 to-indigo-600 hover:from-purple-700 hover:to-indigo-700 text-white font-semibold px-6 py-2 rounded-lg shadow-lg transition-all duration-300"
      >
        ‚ûï Registrar Venta
      </button>

      {open && (
        <div className="modal-overlay">
          <div className="modal-content">
            <button className="modal-close-btn" onClick={() => setOpen(false)}>‚úñ</button>
            <h2 className="text-xl font-bold text-indigo-700 mb-4">Registrar nueva venta</h2>

            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Cliente</label>
              <select
                className="border rounded w-full p-2 focus:ring-indigo-500 focus:border-indigo-500"
                value={cliente?.idCliente || ''}
                onChange={(e) => {
                  const selected = clientes.find(c => c.idCliente === e.target.value);
                  if (selected) setCliente(selected);
                }}
              >
                <option value="">Seleccionar cliente</option>
                {clientes.map(c => (
                  <option key={c.idCliente} value={c.idCliente}>
                    {c.nombre} - {c.correo}
                  </option>
                ))}
              </select>
              {cliente && (
                <p className="text-sm mt-1 text-gray-600">
                  üìû {cliente.telefono} ‚Äì ‚úâÔ∏è {cliente.correo}
                </p>
              )}
            </div>

            <div className="mb-4">
              <p className="text-sm font-semibold mb-1">Servicios disponibles:</p>
              <div className="grid grid-cols-2 gap-3">
                {servicios.map(s => (
                  <button
                    key={s.idServicio}
                    onClick={() => agregarServicio(s.idServicio)}
                    className="border rounded-lg p-3 bg-white hover:bg-indigo-50 text-left transition"
                  >
                    <p className="font-medium text-indigo-800">{s.nombre}</p>
                    <p className="text-sm text-gray-500">{formatCurrency(s.costo)}</p>
                  </button>
                ))}
              </div>
            </div>

            <ul className="mb-4 space-y-2">
              {detalles.map((d) => (
                <li key={d.idDetalleServicio} className="flex justify-between items-center border p-3 rounded-md bg-gray-50">
                  <span className="text-gray-800">{d.nombreServicio}</span>
                  <button onClick={() => eliminarDetalle(d.idServicio)} className="text-sm text-red-600 hover:underline">
                    Quitar
                  </button>
                </li>
              ))}
            </ul>

            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Descuento (S/.)</label>
              <input
                type="number"
                className="border p-2 rounded w-full focus:ring-blue-500 focus:border-blue-500"
                value={descuento}
                min={0}
                onChange={(e) => setDescuento(Number(e.target.value))}
              />
            </div>

            <div className="mb-2 text-right font-bold text-green-700 text-lg">
              Total: {formatCurrency(total)}
            </div>
            {mensaje && <p className="text-green-600 text-sm">{mensaje}</p>}
            {error && <p className="text-red-600 text-sm">{error}</p>}

            <div className="flex justify-end gap-3 mt-5">
              <button onClick={limpiar} className="bg-gray-200 hover:bg-gray-300 text-gray-700 px-4 py-2 rounded-md transition">
                Cancelar
              </button>
              <button
                onClick={handleSubmit}
                className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded-md shadow-sm transition"
                disabled={!cliente || detalles.length === 0}
              >
                Registrar
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default VentaRegistroModal;
